.App {
    min-height: 100vh;
    background-size: cover;
    position: relative;
    transition: 500ms;
    opacity: 1;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.3);
        z-index: 0;
    }

    .container {
        padding: 15px 15px;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-direction: column;

        @media screen and (max-width: 769px) {
            position: relative;
            width: 100%;
            top: initial;
        }
    }
}