.city__list {
    padding-bottom: 10px;
    margin: 40px 0;
    border-bottom: 1px rgb(240, 240, 240) solid;

    .city {
        margin: 30px 0;
        list-style: none;
        cursor: pointer;
        color: rgb(240, 240, 240);
        transition: transform 150ms ease;
        font-size: 20px;
        display: flex;
        justify-content: space-between;
        cursor: pointer;
        color: rgb(240, 240, 240);
        transition: transform 150ms ease;

        &:hover {
            transform: scale(1.05);
            color: rgb(255, 255, 255);
            cursor: pointer;
        }
    }
}